export default {
    data() {
        return {
            refreshing: false,
            loading: false,
            aGalleryLeft: [],
            aGalleryRight: [],
        }
    },
    mounted() {
        // this.$nextTick(() => {
        //     let iMainHeight = $(window).height() - $('#header').height() - $('#footer').height();
        //     $('.main .demo-loadmore-wrap').css({
        //         height: iMainHeight + 'px'
        //     });
        // });
    },
    methods: {
        refresh() {
            this.refreshing = true;
            this.$refs.container.scrollTop = 0;
            setTimeout(() => {
                this.refreshing = false;
            }, 2000)
        },
        load() {
            this.loading = true;
            setTimeout(() => {
                this.loading = false;
            }, 2000)
        }
    }
}

